.container-tree{
    background-color: rgb(10, 10, 32);
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    padding-top: 12rem;
    padding-bottom: 12rem;
    gap: 5rem;
}

.container-tree img{
    margin-right: 100px;
    flex-grow: 7;
    border-radius: 20px;
    animation: white-neon 5s infinite alternate;
}

@keyframes white-neon{
    0%,10%,15%{
        box-shadow: 0 0 10px rgb(66, 167, 26),
        0 0 20px rgba(50, 204, 19, 0.8),
        0 0 30px rgba(105, 221, 10, 0.8),
        0 0 40px rgba(73, 255, 27, 0.8);
    }
    20%,30%,35%{
        box-shadow: 0 0 10px rgb(221, 211, 204),
        0 0 20px rgba(197, 191, 189, 0.8),
        0 0 30px rgba(139, 136, 135, 0.8),
        0 0 40px rgba(99, 99, 99, 0.8);
    }
    40%,50%,55%{
        box-shadow: 0 0 10px rgba(70, 72, 185, 0.8),
                   0 0 20px rgba(24, 49, 190, 0.8),
                   0 0 30px rgba(17, 36, 206, 0.8),
                   0 0 40px rgba(39, 25, 240, 0.8);
    }
    60%,70%,75%{
        box-shadow: 0 0 10px rgba(173, 185, 70, 0.8),
                   0 0 20px rgba(146, 190, 24, 0.8),
                   0 0 30px rgba(108, 163, 20, 0.8),
                   0 0 40px rgba(213, 248, 14, 0.8);
    }
    80%,85%,90%,100%{
        box-shadow: 0 0 10px rgba(185, 145, 70, 0.8),
                   0 0 20px rgba(190, 118, 24, 0.8),
                   0 0 30px rgba(206, 140, 17, 0.8),
                   0 0 40px rgba(240, 147, 25, 0.8);
    }
    10%,15%,60%{
        box-shadow: 0 0 10px rgba(185, 70, 70, 0.8),
                   0 0 20px rgba(190, 24, 32, 0.8),
                   0 0 30px rgba(206, 17, 64, 0.8),
                   0 0 40px rgba(240, 25, 25, 0.8);

    }
}

.container-tree h1{
    text-decoration: underline;
    flex-grow: 3;
    font-size: 8em;
    color: red;
    animation: rojo-neon 2s infinite alternate;
}

@keyframes rojo-neon{
    0%,10%,15%,20%,30%,35%,40%,50%,55%,60%,70%,75%,80%,85%,90%,10%{
        text-shadow: 0 0 10px rgb(255, 29, 29),
        0 0 20px rgba(204, 62, 19, 0.8),
        0 0 30px rgba(161, 20, 4, 0.8),
        0 0 40px rgba(255, 27, 27, 0.8);
    }
    10%,15%,60%{
        text-shadow: 0 0 10px rgba(206, 19, 19, 0.8),
                   0 0 20px rgba(190, 24, 32, 0.8),
                   0 0 30px rgba(206, 17, 64, 0.8),
                   0 0 40px rgba(240, 25, 25, 0.8);

    }
}
.container-tree span{
    text-decoration: underline;
    color: rgb(64, 255, 47);
    animation: verde-neon 2s alternate infinite;
    margin-left: 4em;
    font-size: 9rem;
}


@keyframes verde-neon{
    0%,10%,15%,20%,30%,35%,40%,50%,55%,60%,70%,75%,80%,85%,90%,10%{
        text-shadow: 0 0 10px rgba(93, 185, 70, 0.8),
                0 0 20px rgba(63, 190, 24, 0.8),
                0 0 30px rgba(61, 206, 17, 0.8),
                0 0 40px rgba(93, 240, 25, 0.8);
    }
    10%,15%,60%{
        text-shadow: 0 0 10px rgba(24, 105, 13, 0.8),
                   0 0 20px rgba(104, 190, 24, 0.8),
                   0 0 30px rgba(17, 206, 27, 0.8),
                   0 0 40px rgba(93, 240, 25, 0.8);

    }
}

.carousel{
    width: 500px;
    height: auto;
}
.flex-contenedor{
    background-color: rgb(3, 21, 49);
    padding-top: 25px;
    padding-bottom: 25px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 15px;
}
.flex-contenedor .fcont{
    display: flex;
    flex-direction: column;
}

.fcont #card1 {
    margin-left: 20rem;
    padding: 10px;
    transition: 0.3s ease;
}
.fcont #card1:hover{
    box-shadow: 2px 2px 9px 10px rgba(23, 212, 55, 0.5);
}

.fcont #card2 {
    padding: 10px;
    margin-left: 1rem;
    transition: 0.3s ease;
}

.fcont #card2:hover{
    box-shadow: 2px 2px 9px 10px rgba(200, 243, 10, 0.5);
}

.fcont #card3 {
    padding: 10px;
    margin-left: 18rem;
    transition: 0.3s ease;
}

.fcont #card3:hover{
    box-shadow: 2px 2px 9px 10px rgba(236, 11, 11, 0.5);
}

