.container-login{
    padding-top: 10rem;
    padding-bottom: 10rem;
    display: flex ;
    justify-content: center;
    align-items: center;
    background-color: rgb(2, 22, 51);
}

.container-login .back{
    border-radius: 15px;
    padding: 15px;
    background-color: aliceblue;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    gap: 25px;
}

.back img{
    width: 25rem;
    border-radius: 15px;
}

.form-floating input{
    width: 25rem;
}

label i{
    margin-right: 5px;
}

.container-form{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
