#prin{
    background-color: rgb(4, 11, 26);
    padding-top: 50px;
    padding-bottom: 10rem;
}

section h1{
    font-family: 'Kaushan Script', cursive;
    font-size: 7em;
    text-transform: uppercase;
    font-weight: 900;
    color: rgb(14, 193, 238);
    text-align: left;
    margin-bottom: 20px;
    animation: azul-neon 2s infinite alternate
    
}

@keyframes azul-neon{
    0%,10%,15%,20%,30%,35%,40%,50%,55%,60%,70%,75%,80%,85%,90%,10%{
        text-shadow: 0 0 10px rgb(0, 153, 255),
        0 0 20px rgba(20, 6, 80, 0.8),
        0 0 30px rgba(156, 203, 209, 0.8),
        0 0 40px rgba(25, 46, 240, 0.8);
    }
    10%,15%,60%{
        text-shadow: 0 0 10px rgba(23, 82, 209, 0.8),
                   0 0 20px rgba(19, 152, 204, 0.8),
                   0 0 30px rgba(17, 146, 206, 0.8),
                   0 0 40px rgba(25, 100, 240, 0.8);
    }
}


section h2{
    font-family: 'Pacifico', cursive;
    font-size: 6em;
    text-transform: uppercase;
    font-weight: 900;
    color: rgb(231, 28, 28);
    text-align: right;
    margin-bottom: 20px;
    animation: rojo-neon 2s infinite alternate;
}


@keyframes rojo-neon{
    0%,10%,15%,20%,30%,35%,40%,50%,55%,60%,70%,75%,80%,85%,90%,10%{
        text-shadow: 0 0 10px rgb(168, 72, 34),
        0 0 20px rgba(196, 68, 30, 0.8),
        0 0 30px rgba(173, 57, 44, 0.8),
        0 0 40px rgba(248, 34, 34, 0.8);
    }
    10%,15%,60%{
        text-shadow: 0 0 10px rgba(185, 70, 70, 0.8),
                   0 0 20px rgba(190, 24, 32, 0.8),
                   0 0 30px rgba(206, 17, 64, 0.8),
                   0 0 40px rgba(240, 25, 25, 0.8);

    }
}

#prin .col{
    justify-content: center;
    flex-direction: column;
    display: flex;
}
#prin .img-col{
    justify-content: flex-end;
    margin-top: 100px;
}
#prin img{
    max-width: 100% !important;
    width: 100%;
    border-radius: 25px;
    animation: white-neon 5s alternate infinite;
}

@keyframes white-neon{
    0%,10%,15%{
        box-shadow: 0 0 10px rgb(66, 167, 26),
        0 0 20px rgba(50, 204, 19, 0.8),
        0 0 30px rgba(105, 221, 10, 0.8),
        0 0 40px rgba(73, 255, 27, 0.8);
    }
    20%,30%,35%{
        box-shadow: 0 0 10px rgb(221, 211, 204),
        0 0 20px rgba(197, 191, 189, 0.8),
        0 0 30px rgba(139, 136, 135, 0.8),
        0 0 40px rgba(99, 99, 99, 0.8);
    }
    40%,50%,55%{
        box-shadow: 0 0 10px rgba(70, 72, 185, 0.8),
                   0 0 20px rgba(24, 49, 190, 0.8),
                   0 0 30px rgba(17, 36, 206, 0.8),
                   0 0 40px rgba(39, 25, 240, 0.8);
    }
    60%,70%,75%{
        box-shadow: 0 0 10px rgba(173, 185, 70, 0.8),
                   0 0 20px rgba(146, 190, 24, 0.8),
                   0 0 30px rgba(108, 163, 20, 0.8),
                   0 0 40px rgba(213, 248, 14, 0.8);
    }
    80%,85%,90%,100%{
        box-shadow: 0 0 10px rgba(185, 145, 70, 0.8),
                   0 0 20px rgba(190, 118, 24, 0.8),
                   0 0 30px rgba(206, 140, 17, 0.8),
                   0 0 40px rgba(240, 147, 25, 0.8);
    }
    10%,15%,60%{
        box-shadow: 0 0 10px rgba(185, 70, 70, 0.8),
                   0 0 20px rgba(190, 24, 32, 0.8),
                   0 0 30px rgba(206, 17, 64, 0.8),
                   0 0 40px rgba(240, 25, 25, 0.8);

    }
}

.Description{
    padding: 30px;
    background-color: rgb(4, 27, 41);
}
.Description h3{
    font-size: 5rem;
    color: rgb(19, 173, 19);
    text-shadow: 0 0 10px rgba(70, 185, 99, 0.8),
    0 0 20px rgba(17, 126, 32, 0.8),
    0 0 30px rgba(77, 206, 17, 0.8),
    0 0 40px rgba(39, 218, 54, 0.8);

}
h3 span{
    font-size: 4.8rem;
    color: rgb(248, 248, 8);
    text-shadow: 0 0 10px rgba(173, 185, 70, 0.8),
    0 0 20px rgba(165, 190, 24, 0.8),
    0 0 30px rgba(193, 206, 17, 0.8),
    0 0 40px rgba(226, 240, 25, 0.8);

}

.card{
    width: 18rem;
}

#secon{
    background-color: rgb(12, 19, 43);
    padding-top: 50px;
    padding-bottom: 50px;
}
#secon .col{
    justify-content: center;
    flex-direction: row;
    display: flex;
}
#secon .img-col{
    justify-content: flex-end;
    margin-top: 100px;
}
#secon img{
    max-width: 75% !important;
    width: 75%;
}
.card-1{
    margin-left: 20em;
    transition: 0.3s ease-in;
}
.card-1:hover{
    box-shadow: 2px 2px 9px 10px rgba(236, 11, 11, 0.5);
}
#secon .card-2{
    margin-left: 1em;
    transition: 0.3s ease-in;
}
#secon .card-3{
    transition: 0.3s ease-in;
    margin-left: 17em;
}

.card-2:hover{
    box-shadow: 2px 2px 9px 10px rgba(23, 212, 55, 0.5);
}
.card-3:hover{
    box-shadow: 2px 2px 9px 10px rgba(200, 243, 10, 0.5);
}

#ter{
    background-color: rgb(6, 18, 51);
}

.sign{
    padding: 20px;
    background-color: rgb(8, 45, 66);
}
.sign p{
    font-size: 5rem;
    color: rgb(235, 23, 23);
    text-shadow: 0 0 10px rgba(185, 83, 70, 0.8),
    0 0 20px rgba(126, 17, 17, 0.8),
    0 0 30px rgba(206, 17, 17, 0.8),
    0 0 40px rgba(218, 39, 39, 0.8);
}

#el{
    color: rgb(196, 235, 23);
    text-shadow: 0 0 10px rgba(154, 185, 70, 0.8),
    0 0 20px rgba(124, 126, 17, 0.8),
    0 0 30px rgba(193, 206, 17, 0.8),
    0 0 40px rgba(191, 218, 39, 0.8);
}
#de{
    color: rgb(58, 235, 23);
    text-shadow: 0 0 10px rgba(79, 185, 70, 0.8),
    0 0 20px rgba(48, 126, 17, 0.8),
    0 0 30px rgba(49, 206, 17, 0.8),
    0 0 40px rgba(39, 218, 39, 0.8);
}
#la{
    color: rgb(23, 203, 235);
    text-shadow: 0 0 10px rgba(19, 203, 209, 0.8),
    0 0 20px rgba(17, 122, 126, 0.8),
    0 0 30px rgba(17, 200, 206, 0.8),
    0 0 40px rgba(39, 206, 218, 0.8);
}

.embed-responsive{
    padding-top: 25px;
    padding-bottom: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.embed-responsive iframe{
    border-radius: 25px;
    width: 100em;
    max-width: 100em;
    height: 45em;
    transition: 0.2s ease-out;
}

.embed-responsive iframe:hover{
    box-shadow: 2px 2px 20px 15px rgba(212, 23, 23, 0.5);
}
.embed-responsive iframe:active{
    box-shadow: 2px 2px 20px 15px rgba(23, 143, 212, 0.5);
}