.alert{
    padding-left: 10%;
    padding-right: 10%;
    font-size: 17px;
    font-weight: 700;
    color: rgb(85, 85, 85);
    margin-left: 10%;
    margin-right: 10%;
    transition: 0.2s ease-out;
}

.alert:hover{
    box-shadow: 2px 2px 10px 10px rgb(63, 243, 18);
    border-radius: 25px;
    font-size: 17.5px;
    padding-left: 8%;
    padding-right: 8%;
}

.container-receta{
    padding-top: 20px;
    padding-bottom: 20px;
}
.container-receta .container{
    display: flex;
    justify-content: center;
    align-items: center;
}

.col-sm-6 .card{
    width: 40rem;
    transition: .3s ease-in;
}

.col-sm-6 .card:hover{
    box-shadow: 2px 2px 10px 10px rgb(13, 180, 231);
    border-radius: 25spx;
}

.list-group > #list{
    background-color: aliceblue;
    text-align: left;
    color: black;
}


.card-body .card-text{
    text-align: left;
}
