.bg-dark-blue {
    background-color:  rgb(4, 11, 26); 
}

.nav-link{
    transition: 0.3s ease-in-out;
}
.nav-link:hover{
    font-size: 17px;
    font-weight: 900;
    color: rgb(255, 255, 255);
   text-decoration:5px underline rgb(241, 18, 18);
}

.nav-link:active{
    transition: 0.1s ease;
    font-size: 16px;
    color: rgb(9, 9, 87);
}

.list-group-item{
    transition: 0.3s ease;
    color: whitesmoke;
    background-color: rgb(33, 37, 41);
}

.list-group-item:hover{
    color: black;
    font-weight: 600;
    background-color: whitesmoke;
}

.font-weight-bold{
    font-weight: 900;
    text-transform: uppercase;
    font-size: 20px;
    text-decoration: underline;
}
.row{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row nowrap;
}

.dropdown-item{
    background-color:  rgb(4, 11, 26);
    color: aliceblue;
    transition: 0.2s ease-in-out;
}

.navbar-text .dropdown-item{
    margin-left: 10px;
    margin-right: 10px;
    padding: 0;
    border-radius: 50%;
    position: relative;
    left: 10rem;
    font-size: 2rem;
}

.navbar-text .dropdown-item:hover{
    color: red;
    box-shadow: 1px 1px 5px 5px red;
    transition: .2s ease-in-out;
}
.navbar-text .dropdown-item:active{
    color: rgb(4, 0, 255);
    box-shadow: 1px 1px 5px 5px rgb(0, 89, 255);
}